



.header_logo{
  width: 100%;
  height: 100px;
  background: -webkit-linear-gradient(top , #ffffff , #e2e2e2);
}


.tab{
  height: 40px;
  /*line-height: 40px;*/
  width: 100%;
  background: -webkit-linear-gradient(top , #c95508, #c0000d,#c95508);
  display: -ms-flexbox;
  display: flex;
}
.tab a{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
}
.tab a:hover{
  background: -webkit-linear-gradient(top , #2f2f2f, #f2f2f2);
}
.tab a span{
  display: block;
  width: 100%;
  height:22px;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  border-right: 1px solid #000;
}


